/* Importamos la fuente Poppins desde Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

/* Fondo rosa para la pantalla de login */
.intro-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f0f1;  
  height: 100vh;
  font-family: 'Poppins', sans-serif; 
}

.logo {
  width: 250px; 
  margin-bottom: 30px; 
}

.start-button {
  background-color: #f76c9e; 
  color: white;
  border: none;
  padding: 20px 40px;
  font-size: 22px; 
  font-weight: bold; 
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif; 
}

.start-button:hover {
  transform: scale(1.1);  
  background-color: #f54b7b; 
}

/* Encabezado */
.app-header {
  background-image: url('./images/R.jpeg');
  background-size: cover; 
  background-position: center; 
  background-repeat: no-repeat; 
  text-align: center;
  color: black;
  padding: 50px 0;
  font-family: 'Poppins', sans-serif; 
  position: relative; 
}

.app-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4); 
  z-index: 1; 
}

.app-header h1, 
.app-header p {
  position: relative; 
  z-index: 2; 
}

.app-header h1 {
  font-size: 3.5rem; 
  margin: 0;
  font-weight: 600; 
}

.app-header p {
  font-size: 1.8rem;
  margin-top: 10px;
  font-weight: 400; 
}

/* Fondo general de todo el cuerpo de la app (excepto el encabezado) */
.app {
  background-image: url('./images/BABY.webp'); 
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh; 
  padding-bottom: 20px; 
}

/* Secciones de los botones */
.app-sections {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  padding: 20px;
  font-family: 'Poppins', sans-serif; 
}

.section {
  background-color: rgba(255, 255, 255, 0.8); 
  text-align: center;
  padding: 30px;
  border-radius: 15px;
  cursor: pointer;
  font-weight: bold; 
  font-size: 1.5rem;  
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  text-decoration: none !important; 
}

.section:hover {
  background-color: #f54b7b; 
  transform: scale(1.05);
  color: white;
}

.section span {
  font-size: 2.5rem; 
  margin-bottom: 10px;
}

.section p {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0;
  color: #333;
}

.whatsapp-button {
  background-color: #25d366;
  color: white;
  padding: 20px;
  text-align: center;
  font-size: 1.5rem;
  border-radius: 15px;
  transition: all 0.3s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none !important; 
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
}

.whatsapp-button span {
  font-size: 2.5rem;
  margin-right: 10px;
}

.whatsapp-button:hover {
  background-color: #128c7e;
  transform: scale(1.05);
}

.diario-section {
  padding: 20px;
}

.diario-section h2 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.diario-section .note {
  background-color: #f8c6d4; 
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 400;
}

.diario-section .note p {
  font-size: 1rem;
  margin: 5px 0;
}

.diario-section .note .date {
  font-size: 0.9rem;
  color: #777;
}

button.back-button {
  background-color: #f76c9e;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.3s ease;
  margin-top: 20px;
  font-family: 'Poppins', sans-serif;
}

button.back-button:hover {
  transform: scale(1.1);
  background-color: #f54b7b;
}

/* Elimina subrayado de los enlaces */
.app-sections a, 
.app-sections a:link, 
.app-sections a:visited, 
.app-sections a:hover, 
.app-sections a:active, 
.app-sections .section, 
.whatsapp-button {
  text-decoration: none !important; 
}

/* Alinear el emoji y el texto en los botones */
.section span {
  font-size: 2.5rem; 
  margin-bottom: 10px;
  display: block;
}

.section p {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0;
  color: #333;
}

/* Botón de suscripción a Premium */
.premium-button {
  background-color: #ffd700;
  color: #333;
  padding: 20px 40px;
  font-size: 22px;
  font-weight: bold;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.premium-button:hover {
  transform: scale(1.1);
  background-color: #ffbf00;
}

.premium-button:after {
  content: '✨';
  font-size: 2rem;
  position: absolute;
  top: 10px;
  right: 10px;
  animation: sparkle 2s infinite ease-in-out;
}

@keyframes sparkle {
  0%, 100% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(20deg);
  }
}
