/* Fondo de la página Premium */
.menu-premium-container {
    background-image: url('./images/baby-shower-cute-background-illustration-ai-generative-free-photo.jpg'); /* Usa tu fondo aquí */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 100vh; 
    padding: 20px;
    color: white;
    text-align: center;
    font-family: 'Poppins', sans-serif;
  }
  
  /* Encabezado del menú premium */
  .app-header {
    text-align: center;
    color: #fff;
    padding: 30px 0;
    background-color: #f76c9e;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  }
  
  .app-header h1 {
    font-size: 2.5rem;
    font-weight: bold;
    margin: 0;
  }
  
  .app-sections {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 20px;
  }
  
  .section {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    padding: 20px;
    border-radius: 15px;
    transition: all 0.3s ease;
    cursor: pointer;
    font-weight: bold;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .section:hover {
    background-color: #f76c9e;
    transform: scale(1.05);
    color: white;
  }
  
  .section span {
    font-size: 2.5rem; 
    margin-bottom: 10px;
  }
  
  .section p {
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  .whatsapp-button {
    background-color: #25d366;
    color: white;
    padding: 15px 30px;
    text-align: center;
    font-size: 1.5rem;
    border-radius: 15px;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none !important;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
    font-weight: bold;
  }
  
  .whatsapp-button:hover {
    background-color: #128c7e;
    transform: scale(1.05);
  }
  
  .whatsapp-button span {
    font-size: 2.5rem;
    margin-right: 10px;
  }
  