/* Contenedor principal */
.calendario-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .calendario-container h1 {
    font-size: 2.5rem;
    color: #f76c9e;
    margin-bottom: 20px;
  }
  
  .calendario-container h2 {
    font-size: 1.8rem;
    color: #555;
    margin: 20px 0;
  }
  
  .calendario-container h3 {
    font-size: 1.5rem;
    color: #555;
    margin-top: 30px;
    margin-bottom: 10px;
  }
  
  .calendario-container input[type="text"] {
    width: 70%;
    padding: 10px;
    font-size: 1.2rem;
    border: 2px solid #f76c9e;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  
  .calendario-container input[type="color"] {
    width: 50px;
    height: 50px;
    border: none;
    cursor: pointer;
  }
  
  .agregar-evento-button {
    background-color: #f76c9e;
    color: white;
    padding: 15px 30px;
    font-size: 1.2rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 10px;
    transition: all 0.3s ease;
  }
  
  .agregar-evento-button:hover {
    background-color: #f54b7b;
    transform: scale(1.05);
  }
  
  .lista-eventos {
    list-style: none;
    padding: 0;
    margin: 20px 0;
  }
  
  .lista-eventos li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f9f9f9;
    padding: 10px 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 1.1rem;
    position: relative;
    border-left: 10px solid #f76c9e;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .lista-eventos li button {
    background-color: transparent;
    border: none;
    font-size: 1.5rem;
    color: #f76c9e;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .lista-eventos li button:hover {
    color: #f54b7b;
    transform: scale(1.2);
  }
  
  /* Notificaciones de Toast */
  .Toastify__toast {
    font-size: 1.2rem !important;
  }
  
  .Toastify__toast--success {
    background-color: #28a745 !important;
  }
  
  .Toastify__toast--error {
    background-color: #dc3545 !important;
  }
  
  .Toastify__toast--warn {
    background-color: #ffc107 !important;
  }
  
  .Toastify__toast--info {
    background-color: #17a2b8 !important;
  }
  
  /* Personalización del calendario */
  .react-calendar {
    width: 100% !important;
    max-width: 100%;
    background-color: #ffffff;
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    font-family: 'Poppins', sans-serif;
    font-size: 1.2rem;
  }
  
  .react-calendar__tile {
    border-radius: 10px;
    transition: all 0.3s ease;
    color: black !important;
  }
  
  .react-calendar__tile--now {
    background-color: #f76c9e !important;
    color: white !important;
  }
  
  .react-calendar__tile--active {
    background-color: #f54b7b !important;
    color: white !important;
  }
  
  .react-calendar__tile--now:hover,
  .react-calendar__tile--active:hover {
    background-color: #e43a62 !important;
  }
  
  .react-calendar__month-view__days__day--weekend {
    color: #f76c9e;
  }
  
  .react-calendar__tile:hover {
    background-color: #f54b7b !important;
    color: white !important;
    transform: scale(1.05);
  }
  
  .event-marker {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin-top: 5px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .calendario-container input[type="text"]::placeholder {
    color: #aaa;
    font-size: 1rem;
  }

  .confetti {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
