/* Contenedor general de la sección premium */
.premium-container {
    padding: 20px 40px; 
    text-align: center;
    background-image: url('./images/Fondo-premium.avif'); 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.premium-container h2 {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #4A4A4A; 
    text-shadow: 2px 2px 5px rgba(0,0,0,0.1); 
}

.premium-container p {
    font-size: 1.5rem;
    margin-bottom: 20px;
    color: #555;
}

.premium-sections {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 30px;
}

.premium-section {
    background-color: rgba(255, 255, 255, 0.9);
    padding: 20px;
    border-radius: 15px;
    text-align: center;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    cursor: pointer;
}

.premium-section:hover {
    transform: scale(1.05);
    background-color: #f76c9e;
    color: white;
}

.premium-section span {
    font-size: 2.5rem;
    display: block;
    margin-bottom: 10px;
}

.premium-section p {
    font-size: 1.2rem;
    font-weight: bold;
    color: #4A4A4A;
}

.upgrade-button {
    background: linear-gradient(90deg, #f76c9e, #f54b7b);
    color: white;
    border: none;
    padding: 20px 40px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 8px 20px rgba(0,0,0,0.1);
    margin-top: 20px;
    text-transform: uppercase; 
}

.upgrade-button:hover {
    background: linear-gradient(90deg, #f54b7b, #f76c9e);
    transform: scale(1.1);
}

.back-button {
    background-color: #f76c9e;
    color: white;
    border: none;
    padding: 15px 30px;
    font-size: 18px;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 20px;
}

.back-button:hover {
    background-color: #f54b7b;
    transform: scale(1.05);
}

@media (max-width: 768px) {
    .premium-container h2 {
        font-size: 2rem; 
    }

    .premium-sections {
        grid-template-columns: repeat(1, 1fr);
    }

    .premium-section {
        padding: 15px;
    }

    .premium-section p {
        font-size: 1rem;
    }

    .upgrade-button {
        font-size: 18px;
        padding: 15px 30px;
    }

    .back-button {
        font-size: 16px;
        padding: 10px 20px;
    }
}
