/* Contenedor principal de logros */
.logros-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .logros-container h1 {
    font-size: 2.5rem;
    color: #f76c9e;
    margin-bottom: 20px;
  }
  
  .logros-lista {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .logro-item {
    background-color: #f9f9f9;
    width: 200px;
    padding: 20px;
    border-radius: 10px;
    margin: 10px;
    text-align: center;
    transition: all 0.3s ease;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    position: relative;
  }
  
  .logro-item.bloqueado {
    filter: grayscale(100%);
    opacity: 0.6;
  }
  
  .logro-item.desbloqueado {
    background-color: #e0ffe0;
    border-left: 5px solid #28a745;
  }
  
  .logro-item h3 {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  .logro-item p {
    font-size: 1rem;
    color: #555;
  }
  
  .logro-estado {
    font-size: 1rem;
    margin-top: 10px;
  }
  
  .logro-estado {
    font-weight: bold;
    font-size: 1.2rem;
  }
  
  