/* Contenedor general de la página */
.ruidos-blancos-container {
    background-color: #f8c6d4; /* Rosa claro */
    min-height: 100vh;
    padding: 20px;
    font-family: 'Poppins', sans-serif;
    text-align: center;
  }
  
  /* Botón de regreso */
  .back-button a {
    display: inline-block;
    background-color: #f76c9e;
    color: white;
    padding: 15px 30px;
    text-decoration: none;
    font-weight: bold;
    border-radius: 10px;
    transition: all 0.3s ease;
    margin-bottom: 20px;
  }
  
  .back-button a:hover {
    background-color: #f54b7b;
    transform: scale(1.05);
  }
  
  /* Título principal */
  h2 {
    font-size: 3rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Descripción */
  p {
    font-size: 1.5rem;
    color: #555;
    margin-bottom: 30px;
  }
  
  /* Lista de sonidos */
  .sonidos-lista {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
  
  /* Item de cada sonido */
  .sonido-item {
    background-color: #ffffff;
    border-radius: 15px;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
    padding: 20px;
    text-align: center;
  }
  
  .sonido-item h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  /* Botones de control */
  .play-button, .pause-button, .stop-button {
    background-color: #f76c9e;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 8px;
    margin: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .play-button:hover, .pause-button:hover, .stop-button:hover {
    background-color: #f54b7b;
    transform: scale(1.1);
  }
  